<template>
  <div id="app">
    <navbar></navbar>
    <router-view />
  </div>
</template>

<script>
import Navbar from '@/views/Navbar/index'
import { mapGetters } from 'vuex'
import { getToken } from '@/utils/auth'
import { languageSettingList } from '@/api/home'

export default {
  components: {
    Navbar
  },
  name: 'App',
  data () {
    return {

    }
  },
  computed: {
    ...mapGetters([
      'userInfo',
    ])
  },
  created () {
    let data = sessionStorage.getItem('elementContentMap')
    if (!data) {
      this.getElementContentMap()
    } else {
      if (!window.elementContentList) {
        this.getElementContentMap()
      }
    }
    if (!sessionStorage.getItem('languageList')) {
      this.getLanguageSettingList()
    }
  },
  mounted () {
    let token = getToken()
    if (this.userInfo && token) {
      this.$store.dispatch('GetInfo')
    }
  },
  methods: {
    getLanguageSettingList () {
      languageSettingList().then(res => {
        sessionStorage.setItem('languageList', JSON.stringify(res.data))
      })
    },
    getElementContentMap () {
      let elementContentMap = window.elementContentMap || '{xxxxx_yyyy_ssss10=11120,xxxxx_yyyy_ssss16=11126,xxxxx_yyyy_ssss15=11125}'
      elementContentMap = elementContentMap.slice(1, elementContentMap.length - 2)
      let arr = elementContentMap.split(',')
      let obj = {}
      arr.forEach(item => {
        let ele = item.split('=')
        ele[0] = ele[0].replace(' ', '')
        this.$set(obj, ele[0], ele[1])
      })
      window.elementContentList = obj
      sessionStorage.setItem('elementContentMap', JSON.stringify(obj))
    },
  },
}
</script>

<style lang="scss" scoped>
#app {
  font-family: "Poppins";
  // width: 100%;
  // height: 100%;
  // position: absolute;
  // left: 0;
  // top: 0;
  width: 100vw;
  height: 100vh;
}
</style>



